@use 'base/variables' as v;

html{
    box-sizing: border-box;
    font-size: 62.5%;
}

*, *:before, *:after{
    box-sizing: inherit;
}

body{
    font-family: v.$fuenteParrafos;
    font-size: 1.6rem;
    line-height: 2;
}

/*Globales*/
.contenedor{
    width: min(90%, 120rem);
    margin: 0 auto;
}

a{
    text-decoration: none;
}

h1, h2, h3, h4{
    font-family: v.$fuenteHeading;
    line-height: 1.2;
}

h1{
    font-size: 4.8rem;
}

h2{
    font-size: 4rem;
}

h3{
    font-size: 3.2rem;
}

h4{
    font-size: 2.8rem;
}

img{
    max-width: 120rem;
    width: 100%;
    height: auto;
    display: block;
}

.no-margin{
    margin: 0;
}

.no-padding{
    padding: 0;
}

.centrar-text{
    text-align: center;
}