/*Contacto*/
.webp .contacto-bg {
  background-image: url(../img/contacto.webp);
}

.no-webp .contacto-bg {
  background-image: url(../img/contacto.jpg);
}

.contacto-bg {
  background-image: url(../img/contacto.jpg);
  height: 40rem;
  background-size: cover;
  background-repeat: no-repeat;
}

/*Contacto*/
.formulario {
  background-color: #fff;
  margin: -5rem auto 0 auto;
  width: 95%;
  padding: 5rem;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  gap: 2rem;
}

.campo__label {
  flex: 0 0 9rem;
  text-align: right;
}

.campo__field {
  flex: 1;
  border: 0.1rem solid #e1e1e1;
}

.campo__field--textarea {
  height: 20rem;
}

.error {
  background-color: red;
  text-align: center;
  color: var(--blanco);
  padding: 1rem;
  text-transform: uppercase;
}

.correcto {
  background-color: green;
  text-align: center;
  color: var(--blanco);
  padding: 1rem;
  text-transform: uppercase;
}

/*Curso*/
.curso {
  padding: 3rem 0;
  border-bottom: 0.1rem solid #e1e1e1;
}

@media (min-width: 768px) {
  .curso {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
/*Header*/
.webp .header {
  background-image: url(../img/banner.webp);
}

.no-webp .header {
  background-image: url(../img/banner.jpg);
}

.header {
  background-image: url(../img/banner.jpg);
  height: 60rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.header__text {
  text-align: center;
  color: #fff;
  margin-top: 5rem;
}

.barra {
  padding-top: 4rem;
}

@media (min-width: 768px) {
  .header__text {
    margin-top: 15rem;
  }
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.logo {
  color: #fff;
}

.logo__nombre {
  font-weight: normal;
}

.logo__bold {
  font-weight: bold;
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    gap: 2rem;
  }
}
.navegacion__enlace {
  display: block;
  text-align: center;
  font-size: 1.8rem;
  color: #fff;
}

@media (min-width: 768px) {
  .contenido-principal {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 4rem;
  }
}
.entrada {
  border-bottom: 0.1rem solid #e1e1e1;
  margin-bottom: 2rem;
}

.boton {
  display: block;
  font-family: "PT Sans", sans-serif;
  color: #fff;
  text-align: center;
  padding: 1rem 3rem;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 2rem;
  border: none;
}

.boton:hover {
  cursor: pointer;
}

@media (min-width: 768px) {
  .boton {
    display: inline-block;
  }
}
.boton--primario {
  background-color: #000;
}

.boton--secundario {
  background-color: #784D3C;
}

.cursos {
  list-style: none;
}

.widget-curso {
  border-bottom: 0.1rem solid #e1e1e1;
  margin-bottom: 2rem;
}

.widget-curso:last-of-type {
  border: none;
  margin-bottom: 0;
}

.widget-curso__info, .curso__info {
  font-family: "PT Sans", sans-serif;
  font-weight: bold;
}

.widget-curso__label, .curso__label {
  font-weight: normal;
}

.widget-curso__info, .widget-curso__label, .curso__info, .curso__label {
  font-size: 2rem;
}

.footer {
  background-color: #000;
  padding-bottom: 3rem;
  margin-top: 3rem;
}

/*Sobre Nosotros*/
@media (min-width: 768px) {
  .sobre-nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}